@import url(https://fonts.googleapis.com/css2?family=Karla:wght@200;300;400;500;600;700;800&family=Roboto:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*
===================================================================================================
Body/utils
===================================================================================================
*/

body{
  background-color:#fffefe  !important;
  line-height: 1.64;
}

.arrow {
  border: solid black;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;

}

.down {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.up {
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}


/*
===================================================================================================
Nav Stuff
===================================================================================================
*/
.side-nav{
  position:fixed  !important;
  top: 0;
  left: 0;
  z-index: 999;
}

.nav>a{
  color: #001c00;
  font-family: Roboto;
  font-weight: normal;
  padding: .5rem .8rem !important;

  border-right: .25em solid transparent;
  transition: all 0.3s ease;
  
  

}
.nav>a>strong{
  font-weight: bolder;

}
.nav-item.active,.nav>a:hover{

  border-right: .25em solid sandybrown;
  

}

.side{
  /* background-color:rgb(248, 193, 193); */
  min-height: 100vh;
  overflow: auto;

}


.centre{

  min-height: 100vh;
  overflow-y: hidden;
  overflow-x: hidden  ;

}





/*
===================================================================================================
Hero
===================================================================================================
*/

.hero-img{
  overflow: auto;
  height: 93vh;
}

.hero-img >img{

  width: 100%;
  height: 100%;
  object-fit: cover;
  
}
/*
===================================================================================================
Section Blocks
===================================================================================================
*/

.section{
  min-height: 70vh;
}
.section-title{
  color:white;
  
}

.content{
  background-color: #f8f3f7;
  
}
.section-content{
  color:#605777;

}

/* accordion  */
.acc-continer{
  width:100%;

}

.fold{
  width:100%;
  border-bottom:#052123 3px solid; 
  

  

}

.fold[data-expand="show"]{
  height:  60vh;
  
}
.fold[data-expand="hide"]{
  height:  -webkit-min-content;
  height:  min-content;
  
}
.fold[data-expand="hide"]>.fold-content{
  display: none;
  
}

.fold-head{

  font-family: Karla;
  font-weight: 600;
  padding: 2em 1em;
  

}


.fold-content{
  font-family: Roboto;
  font-weight: 200;

  
}
/* xp block */

.experience{

  
  font-family: Roboto;


}

.experience .col-lg-4 >p{
  letter-spacing: .1em;
  
}


.experience .col-lg-4 > strong{
  
  font-size: 1.1em;
  display: block;
  font-weight: 600;

}
.experience .col-lg-8 > strong{
  
  font-size: 1.0em;
  display: block;
  font-weight: 500;


  
  
}
.experience .col-lg-8 > ul {
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;

}

.experience > div > p {
  font-weight: 300;
  font-size: .81em;

  

}



.about > h5{
  font-family: Karla;
  font-size: 1.2em;
  font-weight: 700;
  line-height: 135%;


}
.about > p{
  font-family: Roboto;
  font-weight: 300;
  line-height: 180%;


  
}



.color{
  background-color: white;
}

.about-card >li{
  padding-bottom: .6em;
}

.about-card >li>strong{
  font-size: 1.1em;
}

.about-card >li>span{
  padding-left: .4em;
}




